

.container {
  max-width: 1100px;
  margin: 0 auto;

  .section {
    width: 70%;
    list-style: none;

  .timeline {
    width: 10%;
    .date {
      color: RGBA(213, 225, 232, 1.00);
    }
    .time {
      color: RGBA(108, 191, 238, 1.00);
    }

  }

  .iconcontainer {
    width: 20%;
    float: right;
    text-align: center;
    margin-bottom: 15px;
    position: relative;

    .outercircle {
      background: black;
      border-radius: 100%;
      width: 45px;
      height: 45px;
      // You always want to "hard code" display properties so that they won't change when the overall pixels are being changed


      .innercircle {
        background: red;
        border-radius: 100%;
        height: 32px;
        width: 32px;
        position: absolute;
        top: 6px;
        left: 6px;

        .logo {

          .fa {
            color: white;
            position: absolute;
            top: 7px;
            left: 8px;
          }

        }

      }
    }
  }

  .box {
    float: right;
    background: RGBA(108, 191, 238, 1.00);
    border: 1px solid black;
    border-radius: 4px;



  }

}

}
